.button--video {
  width: 100%;
  max-width: 800px;
  height: 100%;
  background: none;
  border: none;
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  padding: 0;
  z-index: 2;
  text-align: center;
  text-decoration: none;
  color: inherit;
  background-color: inherit;
  box-sizing: border-box;
  margin: 0;
}
.button--video:hover {
  background: none;
}
@media (min-width: 768px) {
